				"html": "<h1>CSS Selectors</h1>\n<p>In CSS, selectors are patterns used to select DOM elements.</p>\n<p>Here is an example of using selectors. In the following code, <code>a</code> and <code>h1</code> are selectors:</p>\n<pre><code>a {\n  color: black;\n}\nh1 {\n  font-size 24px;\n}\n</code></pre>\n<h2>Cheat list of selectors</h2>\n<p>| Selector | Selects |<br>\n| <code>head</code> | selects the element with the 'head' tag |<br>\n| <code>.red</code> | selects all elements with the 'red' class |<br>\n| <code>#nav</code> | selects the elements with the 'nav' Id |<br>\n| <code>div.row</code> | selects all elements with the div tag and the 'row' class |<br>\n| <code>*</code> | Wildcard selector. Selects all DOM elements. See bellow for using it with other selectors |</p>\n<p>We can combine selectors in interesting ways. Some examples:</p>\n<p>| Selectors | Select |<br>\n| <code>li a</code> | DOM descendant combinator. All aa's tags that are child of li tags |<br>\n| <code>div.row *</code> | selects all elements that are descendant (or child) of the elements with div tag and 'row' class |<br>\n| <code>li > a</code> | Difference combinator. Select direct descendants, instead of all descendants like the descendant selectors |<br>\n| <code>li + a</code> | The adjacent combinator. It selects the element that is immediately preceded by the former element. In this case, only the first <code>a</code> after each <code>li</code>. |<br>\n| <code>li, a</code> | Selects all <code>a</code> elements and all <code>li</code> elements. |<br>\n| <code>li ~ a</code> | The sibling combinator. Selects <code>a</code> element following a <code>li</code> element. |</p>\n<p>Pseudo-selectors or pseudo structural classes are also useful for selecting structural elements from the DOM. Here are some of them:</p>\n<p>| Selectors | Select |<br>\n| <code>:first-child</code> | Target the first element immediately inside (or child of) another element |<br>\n| <code>:last-child</code> | Target the last element immediately inside (or child of) another element |<br>\n| <code>:nth-child()</code> | Target the nth element immediately inside (or child of) another element. Admits integers, <code>even</code>, odd<code>or formulas | |</code>a:not(.name)<code>| Selects all</code>a<code>elements that are not of the</code>.name<code>class | |</code>::after<code>| Allows inserting content onto a page from CSS, instead of HTML. While the end result is not actually in the DOM, it appears on the page as if it is. This content loads after HTML elements. | |</code>::before` | Allows inserting content onto a page from CSS, instead of HTML. While the end result is not actually in the DOM, it appears on the page as if it is. This content loads before HTML elements. |</p>\n<p>We can use pseudo-elements to define a special state of an element of the DOM but don't point to an element by themselves . Some examples:</p>\n<p>| Pseudo-elements | Selects |<br>\n| <code>:link</code> | <code>link</code> pseudo-class selects all links that have not been clicked yet |<br>\n| <code>:hover</code> | <code>hover</code> pseudo-class selects a link that is being hovered by a mouse pointer |<br>\n| <code>:active</code> | <code>active</code> pseudo-class selects a link that is being clicked by a mouse pointer |<br>\n| <code>:visited</code> | <code>visited</code> pseudo-class selects a link that has already been clicked |</p>\n<h2>Games</h2>\n<p><a href=\"http://flukeout.github.io\">CSS Diner</a> is a web game that teaches almost everything there is to know about combining selectors.</p>\n<h2>Additional reference</h2>\n<p>There are many more CSS selectors! Learn about them at <a href=\"http://code.tutsplus.com/tutorials/the-30-css-selectors-you-must-memorize--net-16048\">CodeTuts</a>, <a href=\"https://css-tricks.com/almanac/selectors/\">CSS-tricks.com</a>, <a href=\"http://www.w3schools.com/cssref/css_selectors.asp\">w3schools.com</a> or at <a href=\"https://developer.mozilla.org/en/docs/Web/Guide/CSS/Getting_started/Selectors\">Mozilla Developer Network</a>.</p>\n<p>Feel free to add to this document! Just look cheat sheets up and edit/complete this document!</p>",
